:root {
  --uil-black: #000000;
  --uil-black-alt: #222222;
  --uil-white: #ffffff;
  --uil-grey: #f3f3f3;
  --uil-grey-alt: #cfcfcf;
  --uil-grey-dark: #878787;
  --uil-outline-focus: #1E90FFFF;
  --uil-outline-disabled: #8B0000FF;
  --uil-success: #006A4E;
  --uil-error: #800020;
  --uil-question: #00416A;
  --uil-warning: #FFD700;
  --uil-xxxs: .125rem;
  --uil-xxs: .25rem;
  --uil-xs: .5rem;
  --uil-s: .75rem;
  --uil-m: 1rem;
  --uil-l: 1.25rem;
  --uil-xl: 1.5rem;
  --uil-xxl: 1.75rem;
  --uil-xxxl: 2rem;
  --uil-font-base: var(--uil-l);
  --uil-font-small: var(--uil-m);
}

.uil-check-wrapper {
  display: flex;
  align-items: center;
  gap: var(--uil-s);
  font-size: var(--uil-font-base);
  line-height: var(--uil-font-base);
}
.uil-check-wrapper .uil-checkbox {
  position: relative;
  display: block;
  cursor: pointer;
  font-size: var(--uil-m);
  min-width: var(--uil-m);
  min-height: var(--uil-m);
  user-select: none;
  border: 2px solid var(--uil-black-alt);
  border-radius: 2px;
}
.uil-check-wrapper .uil-checkbox input {
  position: absolute;
  cursor: pointer;
  height: 0;
  width: 0;
  opacity: 0;
}
.uil-check-wrapper .uil-checkbox input:checked ~ .uil-checkmark {
  transform: scale(1);
}
.uil-check-wrapper .uil-checkbox .uil-checkmark {
  position: absolute;
  top: 25%;
  left: 25%;
  background-color: var(--uil-black-alt);
  width: var(--uil-xs);
  height: var(--uil-xs);
  transform: scale(0);
  transition: 0.1s ease;
}